import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AuthService, Helper, Library, VPreviewInvService, sEnum, VStringService, SocketService } from '@core/index';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Subscription } from 'rxjs/Subscription';
import { NgxSpinnerService } from 'ngx-spinner';
import { SendListVoucherComponent, LogProcessVoucherComponent } from '@shared/index';
import { SearchVoucherService } from '@modules/search-vouchers/shared';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
import { VoucherService } from '@modules/vouchers/shared';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  configs: any = {};
  idRow: any = undefined;
  clickCurent: any = undefined;
  dataSource: any;
  _translate: any;
  paramSearch: any;
  extras = [];
  dataInfor: any;
  bsModalRef: BsModalRef;
  bsModalImportRef: BsModalRef;
  bsModalSignRef: BsModalRef;
  ioSignature: Subscription;
  checkSubmit: any;
  templates = [];
  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  invoiceIndex = 0;
  loaiChungTu: string = '01CTT56';
  tempKiFile = 0;
  daXong: boolean = false;
  ganXong: boolean = false;
  select: any = {};
  invoices = [];
  pageSelect: any;
  license: any;
  isExportFailed: boolean = false;
  isTraCuuChungTu: boolean = false;
  textHienThiLuoi: any;
  currentUser: any;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }
  constructor(
    private authService: AuthService,
    public translate: TranslateService,
    private router: Router,
    private searchVoucherService: SearchVoucherService,
    private modalService: BsModalService,
    private socketService: SocketService,
    private vPreviewInvService: ViewInvoiceService,
    private voucherService: VoucherService,
    private spinner: NgxSpinnerService) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.extend,
      items: [
        {
          id: "NAP_CT_TU_EXCEL",
          name: this._translate.CONTROLL.BUTTON.import_license_from_excel,
          icon: this._translate.ICONS.excel
        },

        {
          id: 'SUA',
          name: this._translate.CONTROLL.BUTTON.edit,
          icon: this._translate.ICONS.edit
        },
        {
          id: 'XOA',
          name: this._translate.CONTROLL.BUTTON.delete,
          icon: this._translate.ICONS.delete
        },
        {
          id: 'XEM',
          name: this._translate.CONTROLL.BUTTON.view_voucher,
          icon: this._translate.ICONS.view
        },
        {
          id: 'GUI_BEN_MUA',
          name: this._translate.CONTROLL.LABEL.send_voucher,
          icon: this._translate.ICONS.send_email
        },
        {
          id: 'TAI_CT_PDF',
          name: this._translate.CONTROLL.BUTTON.tai_ct_pdf,
          icon: this._translate.ICONS.download
        },
        {
          id: "TAI_CT_XML",
          name: this._translate.CONTROLL.BUTTON.tai_ct_xml,
          icon: this._translate.ICONS.xml
        }
      ]
    }];
  }

  ngOnInit() {
    this.textHienThiLuoi = "Không có dữ liệu";
    this.configs = this.authService.getConfigs();
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.searchVoucherService.isCopy = false;
    this.currentUser = this.authService.getCurrentUser();
    const listpermissions = this.currentUser.user.permissions.split(',');
    let quyentracuuchungtu = listpermissions.filter(p => p === 'tra_cuu_chung_tu_khau_tru_thue_tncn')[0];
    if (quyentracuuchungtu)
      this.isTraCuuChungTu = true;
    if (!this.isTraCuuChungTu) {
      this.textHienThiLuoi = "Bạn không có quyền thực hiện chức năng này";
    }
  }
  
  selectionChanged(data: any) {
    this.hidenDropdownList();
    this.selectedItems = data.selectedRowsData;
  }
  hidenDropdownList() {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (this.idRow) {
      document.getElementById(this.idRow).classList.remove('show');
    }
  }
  loadList(params?) {
    this.dataSource = {};
    if (this.isTraCuuChungTu) {
      if (params) {
        this.dataSource.store = this.searchVoucherService.getAll(params);
      } else {
        this.dataSource.store = this.searchVoucherService.getAll();
      }
    }
  }
  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);
  }
  onRowPrepared(e) {
    if (e.rowType === 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.license_number) {
            e.rowElement.style.color = 'red';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        const eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }
  view(data) {
    if (this.selectedItems.length > 0 || data) {
      const _lst = this.selectedItems.filter(p => p.status === 'DA_XUAT');
      if (_lst.length > 0 || data) {
        if (this.selectedItems.length === 1) {
          this.dataInfor = this.selectedItems[0];
        } else {
          this.dataInfor = data;
        }
        let ids = [];
        if (data) {
          ids.push(data.id);
        } else {
          ids = _lst.map(e => e.id);
        }
        if (this.configs.system_parameter.view_multi_invoice) {
          let data = this.voucherService.taodulieuXemMau(ids);
          data['configs'] = sEnum.cauhinhxemmau.chungtu;
          this.vPreviewInvService.xemMau(data);
        }
        else {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_chung_tu
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  
  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  
  edit(data) {
    let rowData = null;
    if (data) {
      if (VStringService._getValueToString(data.status) == sEnum.statusInv.DaXuat)
        Library.notify(this._translate.NOTIFY.you_cannot_edit_an_exported_voucher, 'error');
      else
        rowData = data;
    }
    else {
      if (this.selectedItems.length > 0) {
        if (this.selectedItems.length == 1) {
          if (VStringService._getValueToString(this.selectedItems[0].status) == sEnum.statusInv.DaXuat)
            Library.notify(this._translate.NOTIFY.you_cannot_edit_an_exported_voucher, 'error');
          else
            rowData = this.selectedItems[0];
        }
        else {
          let arrStatusInv = ["GHI_TAM"];
          let license = this.selectedItems.map(p => p.status);
          let result = arrStatusInv.every(val => license.includes(val));
          if (result) {
            let recept_no = this.selectedItems.map(s => s.recept_no)
            let arrReceptNoItem = recept_no.reduce((listItem, element) => {
              if (listItem.indexOf(element) === -1) {
                listItem.push(element)
              }
              return listItem
            }, [])
            if (arrReceptNoItem.length == 1) {

            } else {
              Library.notify(this._translate.NOTIFY.same_receipt_license, 'error')
            }
          }
          else {
            Library.notify(this._translate.NOTIFY.you_cannot_edit_an_exported_invoice, 'error')
          }
        }
      }
      else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error')
      }
    }
    if (rowData) {
      this.router.navigate([`/system/voucher-tax/${VStringService._getValueToString(rowData.id)}`]);
    }

  }

  delete(data) {
    if (this.selectedItems.length || data) {
      const status = '';
      let ids = '';
      if (data) {
        ids = data.id;
      } else {
        ids = this.selectedRows.join(',');
      }
      const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.searchVoucherService.delete(ids).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify(this._translate.NOTIFY.delete_success, 'success');
              this.loadList(this.paramSearch);
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    }
  }
  kyHangLoat(data) {

  }
  guibenmua(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    let check = true;
    if (data) {
      if (data.status !== 'DA_XUAT') {
        check = false;
      }
    } else {
      if (this.selectedItems.filter(p => p.status === 'DA_XUAT').length === 0) {
        check = false;
      }
    }

    if (check && (this.selectedItems.length || data)) {
      Library.showloading();
      let ids = [];
      if (data) {
        ids.push(data.id);
      } else {
        ids = this.selectedItems.filter(p => p.status === 'DA_XUAT').map(p => p.id);
      }
      this.searchVoucherService.getListVoucher(ids).subscribe((rs: any) => {
        if (rs.status === 'success') {
          const initialState = {
            licenseList: rs.data.licenses,
            configs: this.configs
          };
          this.bsModalSignRef = this.modalService.show(SendListVoucherComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg full-modal',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            if (this.bsModalSignRef.content.successSaveItem) {
              this.selectedItems = [];
              this.selectedRows = [];
              this.loadList(this.paramSearch);
              subModal.unsubscribe();
            }
          });
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      Library.notify(this._translate.NOTIFY.no_invoice_selected, 'error');
    }
  }
  taiHDXml() {
    if (this.selectedItems.length > 0) {
      const _lst = this.selectedItems.filter(p => p.status == sEnum.statusInv.DaXuat);
      if (_lst.length > 0) {
        let ids = [];
        ids = _lst.map(e => e.id);
        Library.showloading();
        let sub = this.searchVoucherService.xuatCTXml(ids).subscribe(rs => {
          if (rs.status == "success") {
            Helper.dowloadFile(rs.data.file_name, 'zip', rs.data.file_content);
          } else {
            Library.notify(rs.message, "error");
          }
        }, err => { }, () => {
          sub.unsubscribe();
          Library.hideloading();
        });
      }
      else {
        Library.notify(this._translate.NOTIFY.please_choose_voucher_exported, 'error');
        return;
      }
    }
    else {
      Library.notify(this._translate.NOTIFY.please_choose_voucher_exported, 'error');
      return;
    }
  }
  taiHDPdf() {
    if (this.selectedItems.length > 0) {
      const _lst = this.selectedItems.filter(p => p.status == sEnum.statusInv.DaXuat);
      if (_lst.length > 0) {
        this.invoices = _lst.map(e => e.id);
        this.showPopup = true;
        this.totalInvoice = Math.floor(this.invoices.length / 10) + 1;
        this.invoiceIndex = 0;
        this.tempKiFile = 0;
        this.xulyTaiFilePdf();
      }
      else {
        Library.notify(this._translate.NOTIFY.please_choose_voucher_exported, 'error');
        return;
      }
    }
    else {
      Library.notify(this._translate.NOTIFY.please_choose_voucher_exported, 'error');
      return;
    }
  }
  xulyTaiFilePdf() {
    let ids = this.iterator(this.invoices, 10, this.invoiceIndex);

    let sub = this.searchVoucherService.xuatChungTuPdf(ids).subscribe(rs => {
      let isSuccess = false;
      this.ganXong = true;
      this.tempKiFile = 0;
      if (rs.status == 'success') {
        Helper.dowloadFile(rs.data.file_name, 'zip', rs.data.file_content);
        isSuccess = true;
      } else {
        this.isExportFailed = true;
      }
      this.continuteExportInvoice(isSuccess);
    }, err => { }, () => {
      sub.unsubscribe();
      Library.hideloading();
    });
  }
  continuteExportInvoice(isSuccess) {
    let percentComplete = 100 / this.totalInvoice;
    this.valueProgress += percentComplete;
    if (this.invoiceIndex == this.totalInvoice - 1) {
      if (!this.isExportFailed && isSuccess)
        Library.notify("Tải chứng từ thành công", 'success', 5000);
      else {
        // Danh sách lỗi
      }
      this.daXong = true;
      setTimeout(() => {
        this.showPopup = false;
      }, 300);
    }
    else if (this.invoiceIndex < this.totalInvoice - 1) {
      this.invoiceIndex++;
      this.xulyTaiFilePdf();
    }
  }
  createLicense() {
    this.router.navigate(['system/voucher-tax/create']);
  }
  iterator(a, take, skip) {
    let current = skip * 10;
    let l = a.length;
    let end = current + take;
    let part = a.slice(current, end);
    current = end < l ? end : 0;
    return part;
  }
  itemExtrasClick(e) {
    if (e.itemData.id === 'KY_SO') {
      this.kyHangLoat(null);
    } else if (e.itemData.id === 'SUA') {
      this.edit(null);
    } else if (e.itemData.id === 'XOA') {
      this.delete(null);
    } else if (e.itemData.id === 'XEM') {
      this.view(null);
    } else if (e.itemData.id === 'GUI_BEN_MUA') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'LIST_INVOICE_BUY') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'DETAILS_OF_EACH_GOODS_AND_SERVICE') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'ADD_MULTIPLE_INVOICE') {
      this.guibenmua(null);
    } else if (e.itemData.id == "TAI_CT_XML") {
      this.taiHDXml();
    } else if (e.itemData.id == "TAI_CT_PDF") {
      this.taiHDPdf();
    }
  }
  sendVoucher(id) {
    Library.showloading();
    let ids = [];
    ids.push(id);
    let sub = this.searchVoucherService.layDanhSachChungTu(ids).subscribe(rs => {
      if (rs.status == 'success') {
        const initialState = {
          licenses: rs.data.licenses,
        };
        this.bsModalRef = this.modalService.show(LogProcessVoucherComponent, {
          class: 'modal-lg full-modal', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
  trangthaihoadon(data) {
    switch (data.status) {
      case 'DA_XUAT':
        break;
      case 'GHI_TAM':
        if (data.license_number) {
          return this._translate.GRID.INVOICE.signed_error;
        } else {
          return this._translate.CONTROLL.BUTTON.save_temporary;
        }
      case 'GIU_SO':
        return this._translate.GRID.INVOICE.wait_for_sign;
      default:
        break;
    }
  }
  editNotSelect(row) {
    if (row.status === 'DA_XUAT') {
      Library.notify(this._translate.NOTIFY.you_cannot_edit_an_exported_voucher, 'error');
    } else {
      this.router.navigate([`/system/voucher-tax/${row.id}`]);
    }
  }
  viewDetailVoucher(data) {
    this.dataInfor = data;
    let ids = [];
    if (data) {
      ids.push(data.id);
      if (this.configs.system_parameter.view_multi_invoice) {
        let data = this.voucherService.taodulieuXemMau(ids);
        data['configs'] = sEnum.cauhinhxemmau.chungtu;
        this.vPreviewInvService.xemMau(data);
      }
      else {
        const initialState = {
          items: ids,
          isXuatHD: false,
          hinhthucxuat: '',
          typeAction: sEnum.typeCode.xem_chung_tu
        };
        this.vPreviewInvService.xemMauPhanTrang(initialState);
      }
    }
    else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  onImportShowing(e) {
    this.valueProgress = 0;
  }

  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm("Cảnh báo: Quá trình tải hóa đơn sẽ dừng lại nếu bạn đóng cửa sổ này?");
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }
  copy(data) {
    const row = data ? data : this.selectedItems[0];
    let sub = this.searchVoucherService.show(row.id).subscribe(rs => {
      if (rs.status == 'success') {
        if (rs.data.license) {
          rs.data.license.id = null;
          rs.data.license.origin_id = null;
          rs.data.license.status = null;
          rs.data.license.license_number = null;
          rs.data.license.destroy_date = null;
          rs.data.license.destroy_reason = null;
          rs.data.license.adjustment_type = 1;
          rs.data.license.license_sign_date = null;
          rs.data.license.send_email_status = null;
          rs.data.license.verify_code = null;
          if (rs.data.license.license_options && rs.data.license.license_options.length > 0) {
            rs.data.license.license_options.forEach(item => {
              delete item['id'];
              delete item['license_id'];
            });
          }
          this.searchVoucherService.data = rs.data.license;
          this.searchVoucherService.isCopy = true;
        }
        if (this.searchVoucherService.data) {
          this.router.navigate(['system/voucher-tax/create']);
        }
        // if (this.loaiHoaDon === '01GTKT') {
        //   this.router.navigate(['system/vat-invoice/create']);
        // } else {
        //   if (this.loaiHoaDon == sEnum.loaihoadon.KPTQ07)
        //     this.router.navigate(['system/non-tariff-invoice/create']);
        //   else if (this.loaiHoaDon == sEnum.loaihoadon.GTTT02)
        //     this.router.navigate(['system/sale-invoice/create']);
        // }
      }
    });
  }
  /* Xử lý sự kiện click phần mở rộng của từng hóa đơn*/
  myFunction(id) {
    if (this.clickCurent !== '' && this.clickCurent !== id) {
      let valueCurrent = document.getElementById(this.clickCurent);
      if (!valueCurrent)
        document.getElementById(id).classList.toggle('show');
      else {
        document.getElementById(this.clickCurent).classList.remove('show');
        document.getElementById(id).classList.toggle('show');
      }
    }
    else
      document.getElementById(id).classList.toggle('show');
    this.clickCurent = id;
  }
  calculateFilterExpression(filterValue, selectedFilterOperation) {
    selectedFilterOperation = "contains"; //Helper.multilfilterColumn;
    return [["template_code_temp", selectedFilterOperation || '=', filterValue],'or', ["license_series_temp",selectedFilterOperation || '=', filterValue]];
  }
}
